<template>
<div>
  <div class="title centertext">
    Sign-up for a New Account
  </div>
  <message ref="msg"></message>
  <div class="ui middle aligned center aligned grid signup">
    <div class="column">

      <form class="ui attached form" :class="{'error': errors.any(), 'loading': isBusy }">
        <div class="ui error message">
          <div class="header">Validation failed</div>
          <p>{{ errors.first('Company name') }}</p>
          <p>{{ errors.first('City') }}</p>
          <p>{{ errors.first('Area') }}</p>
          <p>{{ errors.first('Address') }}</p>
          <p>{{ errors.first('Phone') }}</p>
          <p>{{ errors.first('Email') }}</p>
          <p>{{ errors.first('Authorized person') }}</p>
          <p>{{ errors.first('Login name') }}</p>
          <p>{{ errors.first('Password') }}</p>
          <p>{{ errors.first('Accept terms and conditions') }}</p>
        </div>

        <div class="ui segment">
        <div class="field" :class="{'error': errors.has('Company name') }">
          <label>Company Name</label>
          <input name="Company name" placeholder="Company Name" type="text" autofocus v-model="companyname" v-validate data-rules="required|min:6|max:50">
        </div>

        <div class="two fields">
          <div class="field" :class="{'error': errors.has('City') }">
            <label>City</label>
            <select class="ui simple dropdown" name="City" v-model.lazy="city" v-validate data-rules="required">

            <option class="item" v-for="city in cityList" :value="city.name">{{city.name}}</option>

              </select>
          </div>
          <div class="field" :class="{'error': errors.has('Area') }">

            <label>Area</label>
            <select class="ui simple dropdown" name="Area" v-model="area" v-validate data-rules="required">
            <option v-for="area in areaList" :value="area._id">{{area.name}}</option>
            </select>
          </div>
        </div>
        <div class="field" :class="{'error': errors.has('Address') }">
          <label>Address</label>
          <input placeholder="Address" type="text" name="Address" v-model="address" v-validate data-rules="required|min:6|max:80">
        </div>
        <div class="two fields">
          <div class="field" :class="{'error': errors.has('Phone') }">
            <label>Phone</label>
            <input placeholder="Phone" type="text" name="Phone" v-model="phone" v-validate data-rules="required|max:10">
          </div>
          <div class="field" :class="{'error': errors.has('Email') }">
            <label>Email</label>
            <input placeholder="Email address" type="email" name="Email" v-model="email" v-validate data-rules="required|email|max:25">
          </div>
        </div>

        <div class="field" :class="{'error': errors.has('Authorized person') }">
          <label>Authorized Person</label>
          <input placeholder="Authorized Person" type="text" name="Authorized person" v-model="username" v-validate data-rules="required|min:6|max:30">
        </div>

        <div class="two fields">
          <div class="field" :class="{'error': errors.has('Login name') }">
            <label>Login name</label>
            <input placeholder="Login name" type="text" name="Login name" v-model="loginname" v-validate data-rules="required|loginname">
          </div>
          <div class="field" :class="{'error': errors.has('Password') }">
            <label>Password</label>
            <input type="password" name="Password" v-model="password" v-validate data-rules="required|password">
          </div>
        </div>
        <div class="inline field">
          <div class="ui checkbox">
            <input type="checkbox" id="terms" name="Accept terms and conditions" v-model="terms" >
            <label for="terms">I agree to the terms and conditions</label>
          </div>
        </div>
        <button class="ui blue submit fluid button" type="submit" @click.prevent="onSubmit()">Submit</button>
      </div>
      </form>
      <div class="ui bottom attached message segment">
        Already signed up? <a @click="$router.push('login')">Login here</a> instead.
      </div>

    </div>
  </div>

</div>
</template>

<script>
'use strict';
export default {
  created () {
    this.isBusy = true
    this.$server.rpc({method: 'city.list'}).then((data) => {
      this.isBusy = false
            this.cityList = data;
           this.city = 'Hyderabad';
        })
        .catch(function (error) {
            this.isBusy = false
            this.$refs.msg.setType('error')
            this.$refs.msg.setText('')
            this.$refs.msg.show()
        });
      },
  data() {
    return {
      isBusy: false,
      cityList: [],
      areaList: [],
      companyname: '',
      city: '',
      area: '',
      address: '',
      phone: '',
      email: '',
      username: '',
      loginname: '',
      password: '',
      terms: false
    }
  },
  methods: {
    onSubmit() {
      this.$validator.validateAll()

      if (!this.errors.any()) {
        if(!this.terms) {
          this.$refs.msg.setType('warning')
          this.$refs.msg.setText('You must agree with terms and conditions')
          this.$refs.msg.show()
          return
        }

        this.isBusy = true;
        let params = {
          method: 'customer.create',
          args: {
            companyName: this.companyname.trim(),
            city: this.city,
            area: this.area,
            address: this.address.trim(),
            phone: this.phone.trim(),
            email: this.email.trim(),
            name: this.username.trim(),
            loginName: this.loginname.trim(),
            password: this.password.trim()
          }
        };

        this.$server.rpc(params).then((data) => {
          this.isBusy = false
          this.$router.push('thankyou')
        }).catch((error) => {
          this.isBusy = false
          let errorMsg;
          if (error.toString().indexOf('email') !== -1) {
              errorMsg = 'Email associated with another account';
          } else if (error.toString().indexOf('loginName') !== -1) {
              errorMsg = 'Login name associated with another account';
          } else {
              errorMsg = error.toString();
          }
          this.$refs.msg.setType('error')
          this.$refs.msg.setText(errorMsg)
          this.$refs.msg.show()
        });
        params = null;

      }
    }
  },
  watch: {
    city () {
      let params = {
        method: 'area.list',
        args: { city: this.city }
          };
      this.isBusy = true
      this.$server.rpc(params).then((data) => {
        this.isBusy = false
              this.areaList = data;
              })
          .catch(function (error) {
              this.isBusy = false
              this.$refs.msg.setType('error')
              this.$refs.msg.setText('Unable to load area list')
              this.$refs.msg.show()
          });
          params = null;
    }
  }

}
</script>

<style scoped>
option {
  font-size: 1.1em;
  }

.signup {
  margin: auto;
}

form {
  text-align: left;
}

h1,
h3 {
  color: white;
  margin: 0;
}

h1 {
  font-size: 3em;
}

#contents .column {
  padding-right: 2rem !important;
}

@media all and (min-width: 0) and (max-width: 480px) {
  .signup {
    width: 100%;
    padding: 0em;
    padding-top: 1rem;
    height: auto;
  }
}

@media all and (min-width: 481px) and (max-width: 840px) {
  .signup {
    width: 70%;
  }
}

@media all and (min-width: 841px) and (max-width: 1280px) {
  .signup {
    width: 40%;
  }
}

@media all and (min-width: 1281px) and (max-width: 1600px) {
  .signup {
    width: 35%;
  }
}
</style>



